html, body, #root{
    width: 100%;
    min-height: 100%;
    background-image: url('../images/back3.jpg');
    background-repeat: no-repeat;
    background-size: contain;
}
.App{
    width: 100% !important;
    height: 100% !important;
    min-width: 100%;
    min-height: 100%;
}

#main{
    background-color: rgba(0,0,0,.6);
    width: 100%;
    height: 100%;
    padding: 50px;
}

.button{
    width: 150px !important;
    border: 1px dotted #00AFED !important;
    background-color: transparent !important;
    margin: 30px;
    color: white;
    padding: 15px 30px;
}
.button:hover{
    background-color:  #00AFED !important;
}
.button:active {
    outline: none;
    border: none;
}
    
.button:focus {outline:0;}

.info{
    background-color: #004fb4;
    padding: 50px;
    border: 1px dotted white;
}

p{
    color: #fff;
    text-align: left;
}

h1{
    color: #fff;
}


.individual{
    width: 100%;
    min-height: 100%;
    padding: 50px;
    background-color: rgba(0,0,0,.6);
}


form {
    padding: 30px 50px;
}

span{
    color: red;
}

form p{
    color: #000;
    font-weight: bold;
}

.form-group{
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    margin-bottom: 50px;
}

.formCont{
    width: 70%;
    background-color: #fff;
    padding: 0;
}

.formCont header{
    background-color: #004fb4;
    padding: 20px;
    color: #fff;
}

.formCont #note{
    color: #000;
    padding: 30px 50px 0;
}

form .button{
    align-self: flex-start;
    background-color: #00AFED !important;
}